.chatting-page-wrapper {
    background: rgba(0, 0, 0, 0.0470165);
    padding: 20px 0;

    .chat-wrapper {
        display: flex;
        background: white;
        height: 80vh;

        .user-list {
            width: 440px;
            height: 100%;
            overflow-y: auto;

            .user {
                padding: 24px 14px;
                border: 1px solid transparent;
                width: 100%;
                background: rgba(182, 202, 251, 0.125113);
                display: flex;
                align-items: center;
                justify-content: space-between;

                .left-side {
                    display: flex;
                    align-items: center;

                    img {
                        width: 40px;
                        height: 40px;
                        margin-right: 16px;
                    }

                    .info {
                        p {
                            font-size: 12px;
                            letter-spacing: 0.2px;
                            color: #25252B;
                            margin-bottom: 5px;
                        }

                        h3 {
                            font-family: "Gilroy bold", sans-serif;
                            font-weight: lighter;
                            font-size: 15px;
                            color: #25252B;
                        }
                    }
                }

                .right-side {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;

                    button {
                        background: transparent;
                        outline: none;
                        border: none;

                        img {
                            width: 8px;
                            height: 14px;
                        }
                    }

                    span {
                        font-size: 12px;
                        letter-spacing: 0.2px;
                        color: #686871;
                    }
                }

                &.active {
                    background: white;
                    border: 1px solid #E1E6ED;
                }
            }
        }

        .chat-section {
            width: calc(100% - 440px);
            padding: 50px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .chatting-section-top {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                .block {
                    display: flex;
                    align-items: center;

                    input {
                        border: none;
                        background: transparent;
                        outline: none;
                        border-bottom: 1px solid #686871;
                        padding: 5px;
                    }

                    &:first-of-type {
                        img {
                            margin-right: 15px;
                        }
                    }

                    button {
                        margin-left: 15px;
                        background: none;
                        outline: none;
                        border: none;
                    }
                }
            }

            .chatting-section-bottom {
                width: 100%;

                input {
                    width: 100%;
                    height: 100px;
                    background: transparent;
                    outline: none;
                    border-radius: 5px;
                    border: 1px solid #686871;
                }

                .sending-part {
                    margin-top: 15px;
                    display: flex;
                    justify-content: space-between;

                    button {
                        background: transparent;
                        outline: none;
                        border: none;
                    }

                    .send {
                        font-size: 14px;
                        color: #FFFFFF;
                        padding: 10px 20px;
                        background: #41D481;
                        box-shadow: 0px 2px 24px rgba(72, 128, 255, 0.2);
                        border-radius: 20px;
                    }
                }
            }
        }
    }
}